<template>
  <div id="app">
    <Header>
      <h1 class="el-title" style="text-align: center">
        <a class="head_title" href="/"> 签到<sup>alpha</sup> </a>
      </h1>
    </Header>
    <Content>
      <div style="margin-bottom: 20px">
        <div
          class="code_title"
          style="box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04)"
        >
          Python 源码
        </div>
        <div class="codemirror">
          <!-- codemirror -->
          <CodeMirror v-model="form.har" />
        </div>
      </div>

      <div
        class="submit"
        style="text-align:right;margin-right: 10px;margin-bottom: 20px"
      >
        <Button class="sub" style="margin-right: 10px;" @click="download()">
          <a
            target="_blank"
            :href="file.href"
            :download="file.filename"
            style="color: #515A7A"
            >下载</a
          >
        </Button>
        <Button
          class="submit"
          @click="test_click()"
          style="margin-right: 10px;"
        >
          测试
        </Button>
        <Button
          class="submit"
          style="margin-right: 10px;"
          type="primary"
          @click="save()"
        >
          保存
        </Button>
      </div>

      <!--        <Modal v-model="submit" title="Welcome">Welcome to ViewUI</Modal>-->
      <Modal
        v-model="submit"
        title="请输入配置"
        :closable="false"
        @on-ok="handleSubmit"
        :loading="true"
        :mask-closable="loading === true"
      >
        <Form :model="form" :label-width="80">
          <FormItem label="网站名">
            <Input type="text" v-model="setting.sitename" />
          </FormItem>
          <FormItem label="网站网址">
            <Input type="text" v-model="setting.siteurl" />
          </FormItem>
          <FormItem label="变量配置" style="margin-bottom: 10px;">
            <!-- <Input type="text" v-model="form.variables"></Input> -->
            <!-- <div>
              <Table border :columns="columns" :data="variables">
                <template slot-scope="{ row, index }" slot="action">

                </template>
              </Table>
            </div> -->

            <Button type="primary" size="small" @click="edit_variable()">
              添加
            </Button>
            <div>
              {{ variablesObj }}
            </div>

            <Modal
              v-model="edit_var"
              title="请输入配置"
              :closable="false"
              @on-ok="handleSavaVar"
            >
              <div slot="header">
                <!-- <Icon type="ios-information-circle"></Icon> -->
                <span>变量编辑</span>
                <Button
                  type="primary"
                  size="small"
                  style="margin-right: 5px;float:right"
                  @click="add_var()"
                  >添加</Button
                >
              </div>

              <Table :data="variables" :columns="columns" border></Table>
            </Modal>
          </FormItem>
          <FormItem label="备注">
            <Input type="text" v-model="setting.note" />
          </FormItem>
          <FormItem label="间隔">
            <Select v-model="setting.interval" style="width:200px">
              <Option :value="604800">
                每周
              </Option>
              <!--                        <Option value=259200>每3天</Option>-->
              <Option :value="86400">
                每天
              </Option>
              <!--                        <Option value=79200>每22小时</Option>-->
              <Option :value="43200">
                每12小时
              </Option>
              <Option :value="21600">
                每6小时
              </Option>
              <!--                        <Option value=18000>每5小时</Option>-->
              <!--                        <Option value=10800>每3小时</Option>-->
              <!--                        <Option value=7200>每2小时</Option>-->
              <Option :value="3600">
                每小时
              </Option>
              <Option :value="-1">
                自定义cron
              </Option>
            </Select>
          </FormItem>
          <FormItem label="cron" v-show="setting.interval === -1">
            <!--                    <Input type="text" v-model="setting.cron"/>-->
            <InputCron
              type="text"
              v-model="setting.cron"
              placeholder="请输入cron表达式,或者点击右侧配置"
            />
          </FormItem>
        </Form>
      </Modal>

      <Modal
        v-model="btn_test"
        title="请输入变量配置"
        :closable="false"
        @on-ok="test"
        :loading="true"
        :mask-closable="loading === true"
      >
        <div slot="header">
          <!-- <Icon type="ios-information-circle"></Icon> -->
          <span>变量编辑</span>
          <Button
            type="primary"
            size="small"
            style="margin-right: 5px;float:right"
            @click="add_var()"
            >添加</Button
          >
        </div>

        <Table :data="variables" :columns="columns" border></Table>
      </Modal>
    </Content>
    <Footer>
      <p class="text-center" style="text-align: center;">
        a <a href="http://binux.me/" target="_blank">Binux</a>'s project. Power
        By
        <a target="_blank" href="https://github.com/Gahon1995">Gahon</a>
      </p>
    </Footer>
  </div>
</template>

<script>
// import HelloWorld from "./components/HelloWorld.vue";
import InputCron from "./components/easy-cron/input-cron.vue";
import CodeMirror from "./components/code-mirror/code-mirror.vue";
import {
  getGlue,
  saveGlue,
  testGlue,
  getTemp /*, getUserId  */
} from "./api/gule.js";

let Base64 = require("js-base64").Base64;

export default {
  name: "App",
  components: {
    InputCron,
    CodeMirror
  },
  created() {
    let s = window.location.href;
    this.id = s.substr(s.lastIndexOf("edit") + 5);
    // console.log(this.id);
    if (this.id !== "") {
      getGlue(this.id).then(response => {
        // console.log(response);
        this.form.har = response.har;
        this.form.variables = response.env;
        this.form.setting = response.setting;
        // this.form.har = response.har;
        // this.$set(this, "har", response.har);
      });
    } else {
      getTemp().then(res => {
        this.form.har = res;
      });
    }
  },
  data() {
    return {
      id: null,
      har: "",
      btn_test: false,
      editName: "",
      editDesc: "",
      editIndex: -1,
      loading: true,
      editor: null,
      submit: false,
      edit_var: false,
      variables: [],
      file: {
        href: "",
        filename: "test.py"
      },
      variablesObj: {},
      form: {
        har: "",
        variables: {},
        setting: {
          sitename: "测试网址",
          siteurl: "www.test.com",
          note: "",
          type: "1",
          cron: "0 0 0 01",
          interval: 86400
        }
      },
      setting: {
        sitename: "测试网址",
        siteurl: "www.test.com",
        note: "",
        type: "1",
        cron: "0 0 0 01",
        interval: "86400"
      },
      columns: [
        // {
        //   type: "index",
        //   width: 100,
        //   align: "center",
        //   title: "ID",
        //   key: "id"
        // },
        {
          title: "变量名",
          key: "name",
          render: (h, { row, index }) => {
            let edit;
            if (this.editIndex === index) {
              this.editName = row.name;
              edit = [
                h("Input", {
                  props: {
                    value: row.name
                  },
                  on: {
                    input: val => {
                      this.editName = val;
                    }
                  }
                })
              ];
            } else {
              edit = row.name;
            }
            return h("div", [edit]);
          }
        },
        {
          title: "描述",
          key: "desc",
          render: (h, { row, index }) => {
            let edit;
            if (this.editIndex === index) {
              this.editDesc = row.desc;
              edit = [
                h("Input", {
                  props: {
                    value: row.desc
                  },
                  on: {
                    input: val => {
                      this.editDesc = val;
                    }
                  }
                })
              ];
            } else {
              edit = row.desc;
            }
            return h("div", [edit]);
          }
        },
        {
          title: "操作",
          render: (h, { row, index }) => {
            if (this.editIndex === index) {
              return [
                h(
                  "Button",
                  {
                    props: {
                      type: "success",
                      size: "small"
                    },
                    on: {
                      click: () => {
                        this.variables[index].name = this.editName;
                        this.variables[index].desc = this.editDesc;
                        this.editIndex = -1;
                      }
                    }
                  },
                  "保存"
                ),
                h(
                  "Button",
                  {
                    props: {
                      type: "error",
                      size: "small"
                    },
                    style: {
                      marginLeft: "6px"
                    },
                    on: {
                      click: () => {
                        if (
                          (this.editName === "" &&
                            this.variables[this.editIndex].name === "") ||
                          this.variables[this.editIndex].name === ""
                        ) {
                          this.variables.splice(this.editIndex, 1);
                        }

                        this.editIndex = -1;
                      }
                    }
                  },
                  "取消"
                )
              ];
            } else {
              return [
                h(
                  "Button",
                  {
                    props: {
                      size: "small"
                    },

                    on: {
                      click: () => {
                        this.editName = row.name;
                        this.editDesc = row.desc;
                        this.editIndex = index;
                      }
                    }
                  },
                  "修改"
                ),
                h(
                  "Button",

                  {
                    props: {
                      type: "error",
                      size: "small"
                    },
                    style: {
                      marginLeft: "6px"
                    },
                    on: {
                      click: () => {
                        this.remove(index);
                      }
                    }
                  },
                  "删除"
                )
              ];
            }
          }
        }
      ],
      test_form: {}
    };
  },
  methods: {
    handleSubmit() {
      // this.form.har = this.har;
      this.form.setting = this.setting;
      this.form.setting.type = "1";
      this.form.variables = this.variablesObj;
      // console.log(this.form);
      saveGlue(this.id, this.form).then(res => {
        // console.log(res);
        this.$Message.success("保存成功");
        this.submit = false;
        window.location.href = "/glue/edit/" + res.id;
      });
    },
    handleSavaVar() {
      // console.log(this.variables);
      // this.variablesObj = {};
      this.variables.map(item => (this.variablesObj[item.name] = item.desc));
    },
    remove(index) {
      this.variables.splice(index, 1);
    },
    add_var() {
      if (this.editIndex != -1) {
        this.$Message.warning("请先保存上一条数据");
      } else {
        this.editIndex = this.variables.push({ name: "", desc: "" }) - 1;
      }
    },
    edit_variable() {
      this.variables.length = 0;
      for (var key in this.variablesObj) {
        this.variables.push({ name: key, desc: this.variablesObj[key] });
      }

      this.edit_var = true;
    },
    save() {
      this.setting = Object.assign({}, this.form.setting);
      this.variablesObj = Object.assign({}, this.form.variables);

      this.submit = true;
    },
    test_click() {
      for (var key in this.variablesObj) {
        this.test_form[key] = "";
      }
      this.btn_test = true;
    },
    test() {
      let test_data = {};
      this.variables.map(item => (this.variablesObj[item.name] = item.desc));
      test_data["code"] = this.form.har;
      test_data["variables"] = Object.assign({}, this.variablesObj);

      // console.log(test_data);
      testGlue(test_data)
        .then(res => {
          // console.log(res);
          if (res["success"]) {
            this.$Message.success("测试成功");
          } else {
            this.$Modal.error({
              title: "测试失败",
              content: res["msg"]
            });
          }
          this.btn_test = false;
        })
        .catch(() => {
          this.btn_test = false;
        });
    },
    download() {
      let encode = Base64.encode(this.form.har);
      this.file.href = "data:application/json;base64," + encode;
      if (this.form.setting.sitename != "") {
        this.file.filename = this.form.setting.sitename + ".py";
      }
    }
  }
};
</script>

