import request from '@/utils/request'

const base = '/glue/edit'

export function getGlue(id) {
  return request({
    url: '/glue/edit/' + id,
    method: 'post'
  })
}

export function saveGlue(id, data) {
  return request({
    url: '/glue/save/' + id,
    method: 'post',
    data: data
  })
}


export function getTemp() {
  return request({
    url: '/glue/save/',
    method: 'get'
  })
}


export function getUserId() {
  return request({
    url: base + '/userinfo',
    method: 'get'
  })
}


export function testGlue(data) {
  return request({
    url: '/glue/test',
    method: 'post',
    data: data
  })
}
